@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: White;
        /* White background */
        --color-bg-variant:#5b86d7;
        /* Slightly off-white background variant */
        --color-primary: #5b86d7;
        /* Blue primary color */
        --color-primary-variant: #354569;
        /* Slightly darker variant of the primary color */
        --color-white: #071d41;
        /* White */
        --color-light: #30415f;
    --transition: all 400ems ease;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}


body {
    font-family: 'Poppins', sans-serif;
    background: rgb(206, 217, 222);
    color: var(--color-white);
    line-height: 1.7;
/*background-image: url(../src/assets/bg-texture.png);*/
}


/* General Styles*/

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
}

section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light)
}

a {
    color: var(--color-primary);
   
    
}

a:hover {
    color: var(--color-white);
    transform: scale(1.3);
}

.btn {
    width: max-content;
    display: inline;
    background-color: var(--color-primary);
    color:black;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background-color: var(--color-primary);
    color:black;
}

img {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;
}


/* Media queries (devices with medium screen)*/

@media screen and (max-width:1024px) {
    .container {
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }
}


/* Media queries (devices with small screen)*/

@media screen and (max-width:600px) {
    .container {
        width: var(--container-width-sm);
    }
    section>h2 {
        margin-bottom: 2rem;
    }
}